export default {
	props: {
		dataSource: {
			type: Object,
			default: {}
		}
	},
	methods: {
		backHandle(e) {
			this.$emit('backHandle', e)
		},
		detailHandle(e) {
			this.$emit('detailHandle', e)
		},
		completeHandle(e) {
			this.$emit('completeHandle', e)
		}
	}
}
